body {
  margin: 0;
}
.wrap {
  display: flex;
}
#headlogo {
  width: 120px;
  height: 143px;
}
.header {
  background-color: #a71b21 !important;
}
.head {
  margin-left: auto;
  margin-right: auto;
  justify-content: space-between;
  align-items: center;
}
.headcont1 {
  color: #fcf3e8;
  font-size: 24px;
}
.headlink {
  padding: 0;
  text-decoration: none;
  margin-left: 20px;
  margin-right: 20px;
  color: #fcf3e8;
  font-size: 22px;
}

.headlink:hover {
  color: #b7a994;
}

.headnum {
  align-items: center;
  padding-bottom: 15px;
}
#headcall {
  width: 50px;
  height: 50px;
}
.sec_main {
  background-image: url(/img/bgi.PNG);
  background-size: cover;
  background-repeat: no-repeat;
  padding: 70px;
}
.info {
  padding-bottom: 20px;
  padding-top: 20px;
  width: 450px;
  align-items: center;
}
.info_text {
  color: #fcf3e8;
  padding-left: 30px;
  font-size: 22px;
}
.all {
  width: 70%;
  margin-left: auto;
  margin-right: auto;
}
#sec_name {
  font-size: 64px;
  color: #fcf3e8;
  margin: 0;
  padding-bottom: 50px;
  text-align: center;
}
.form_ent {
  width: 450px;
  background-color: #fcf3e8;
}
.inp_form {
  padding: 15px;
  width: 300px;
}
.main_form {
  justify-content: space-around;
}
#btn {
  border-radius: 100px;
  border: none;
  background-color: #a71b21;
  width: 340px;
  color: #fcf3e8;
  font-size: 22px;
  font-weight: 600;
}
#btn:hover {
  background-color: #831a1f;
}

.act_card {
  display: flex;
  width: 621px;
  height: 150px;
  /* border: 1px solid black; */
  background-color: #fff;
  margin: 8px;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.sec_act {
  padding: 3%;
  background-color: #fcf3e8;
}

#sec_name2 {
  font-size: 64px;
  color: #000000;
  margin: 0;
  text-align: center;
}

.wrapper {
  width: 70%;
  /* height: 1px; */
  margin-left: auto;
  margin-right: auto;
  border-bottom: 1px solid #000;
}

.slider-card {
  /* width: 500px;
  height: 200px; */
  background-color: gray;
}
.card_img {
  object-fit: cover;
  width: 210px;
  object-position: bottom;
}
.card_text {
  padding: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.cards {
  padding-top: 50px;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}

/* .card_img:nth-child(4) {

} */

.card_text_info {
  font-size: 20px;
  margin: 0;
}

.card_text_com {
  margin: 0;
  font-size: 14px;
}

.slider-block {
  width: 80%;
  height: 300px;
  background-color: red;
  margin-left: auto;
  margin-right: auto;
  float: none;
}

.footer {
  background-color: #fcf3e8;
}

.footer-info {
  flex-wrap: wrap;
  width: 50%;
}

.info-block {
  display: flex;
  width: 350px;
}

.info-block__img {
  width: 50px;
  height: 50px;
  margin-right: 10px;
}

.footer-social {
  width: 50px;
  height: 50px;
  margin-right: 10px;
}
